.table_grid {
  display: none; }
  @media (min-width: 768px) {
    .table_grid {
      display: block;
      width: 720px;
      background: #fff;
      white-space: nowrap;
      margin: 0 auto;
      border-radius: 6px; } }
  @media (min-width: 1120px) {
    .table_grid {
      width: 1056px; } }

.table_grid__title {
  display: grid;
  grid-template-rows: 64px;
  grid-template-columns: 5fr 1fr 1fr 1fr; }

.table_grid__row {
  display: grid;
  grid-template-rows: 64px;
  grid-template-columns: 6fr 2fr 2fr 1fr;
  border-bottom: 1px solid #EAEAEA; }
  @media (min-width: 768px) {
    .table_grid__row {
      margin-left: 24px;
      margin-right: 24px; } }

.table_grid__cell {
  display: flex;
  align-items: center;
  font-family: 'TT Lakes Medium';
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  padding: 0;
  color: #1B1C21; }

.table_grid--header {
  display: none; }
  @media (min-width: 768px) {
    .table_grid--header {
      display: block;
      background: #F2F2F2;
      width: 720px;
      white-space: nowrap;
      margin: 0 auto; } }
  @media (min-width: 1120px) {
    .table_grid--header {
      width: 1056px; } }

.table__btn--form {
